<template>
  <div>
    <h3> 部门管理</h3>
    <Divider orientation="left">部门详情</Divider>
    <Row style="margin-left: 50px">
      <Col :span="6">
        <Card>
          <div style="text-align: right">
            <Button type="primary" shape="circle" icon="md-add" @click="add(0)"></Button>

          </div>
          <Tree :data="columns" :render="renderContent"></Tree>
        </Card>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>
      <Col :span="1">
        &nbsp;
      </Col>
      <Col :span="17">
        <Card>
          <Divider orientation="left">人员管理</Divider>
          <Table stripe border :columns="mansColumns" :data="mans.data"></Table>
          <Spin size="large" fix v-if="loadManIng"></Spin>
          <Row v-if="mans.last_page > 1" style="text-align: center;margin-top: 20px">
            <Page @on-change="loadMans()" :total="mans.total" :page-size="mans.per_page"
                  :current="mans.current_page" size="small"/>
          </Row>
        </Card>

      </Col>
    </Row>

  </div>
</template>

<script>
import api from './api'

export default {
  name: 'index',
  data () {
    return {
      formData: {},
      listData: {},
      mans: {},
      mansColumns: [
        {
          title: '创建日期',
          align: 'center',
          key: 'create_time'
        },
        {
          title: '姓名',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.user.nickname)
          }
        },
        {
          title: '角色',
          key: 'role',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('span', params.row.role === 0 ? '成员' : '负责人'),
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginLeft: '5px',
                  display: params.row.role === 0 ? '' : 'none'
                },
                on: {
                  click: () => {
                    api.leader({ id: params.row.user.id }, () => {
                      this.loadMans()
                    })
                  }
                }
              }, '设置为负责人')
            ])
          }
        }
      ],
      load: false,
      loadManIng: false,
      columns: [],
      selectItem: {}
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData () {
      this.load = true
      this.ref()
      console.log('-----')
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
        this.initTree(this.listData, this.columns)
        this.columns[0].selectItem = true
        this.selectItem = this.columns[0]
        this.loadMans(1)
      },
      () => {
        this.load = false
      })
    },

    renderContent (h, { root, node, data }) {
      return h('span', {
        style: {
          display: 'inline-block',
          width: '100%'
        }
      }, [
        h('span', [
          h('span', {
            class: `tree-item ${data.selectItem ? 'tree-select-item' : ''}`,
            style: {},
            on: {
              click: () => {
                this.$set(this.selectItem, 'selectItem', false)
                this.$set(data, 'selectItem', true)
                this.selectItem = data
                this.loadMans(1)
              }
            }
          }, data.title)
        ]),
        h('span', {
          style: {
            display: 'inline-block',
            float: 'right',
            marginRight: '32px'
          }
        }, [
          h('Button', {
            props: Object.assign({}, {
              icon: 'ios-create-outline'
            }),
            style: {
              marginRight: '8px',
              padding: '0',
              border: 'none',
              fontSize: '1.2rem'
            },
            on: {
              click: () => {
                this.edit(data)
              }
            }
          }),
          h('Button', {
            props: Object.assign({}, {
              icon: 'ios-add-circle-outline'
            }),
            style: {
              marginRight: '8px',
              padding: '0',
              border: 'none',
              fontSize: '1.2rem'
            },
            on: {
              click: () => {
                this.add(data.id)
              }
            }
          }),
          h('Button', {
            props: Object.assign({}, {
              icon: 'ios-remove-circle-outline'
            }),
            style: {
              padding: '0',
              border: 'none',
              fontSize: '1.2rem'
            },
            on: {
              click: () => {
                this.del(data)
              }
            }
          })
        ])
      ])
    },

    initTree (list, columns) {
      let _this = this
      if (list != null && list.length > 0) {
        list.forEach((value) => {
          let item = {
            title: value.name,
            description: value.description,
            id: value.id,
            selectItem: false,
            expand: true,
            children: []
          }
          if (value.son != null) {
            _this.initTree(value.son, item.children)
          }
          columns.push(item)
        })
      }
    },
    edit (data) {
      this.name = data.title
      this.description = data.description
      this.$Modal.confirm({
        title: '请填写资料',
        okText: '确定',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.save({ name: this.name, description: this.description, id: data.id }, () => {
            this.$Modal.remove()
            this.$Modal.loading = false
            this.$Message.success('添加成功')
            this.loadData()
            this.name = ''
            this.description = ''
          }, () => {
            this.$Modal.remove()
            this.name = ''
            this.description = ''
          })
        },
        onCancel: () => {
          this.name = ''
          this.description = ''
        },
        render: (h) => {
          return h('div', {}, [
            h('Input', {
              props: {
                value: this.name,
                autofocus: true,
                placeholder: '请输入部门名称'
              },
              on: {
                input: (val) => {
                  this.name = val
                }
              }
            }),
            h('Input', {
              props: {
                value: this.description,
                autofocus: true,
                placeholder: '请输入部门简介',
                type: 'textarea'
              },
              style: {
                marginTop: '10px'
              },
              on: {
                input: (val) => {
                  this.description = val
                }
              }
            })
          ])
        }
      })
    },
    add (id) {
      this.$Modal.confirm({
        title: '请填写资料',
        okText: '确定',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.save({ name: this.name, description: this.description, p_id: id }, () => {
            this.$Modal.remove()
            this.$Modal.loading = false
            this.$Message.success('添加成功')
            this.loadData()
            this.name = ''
            this.description = ''
          }, () => {
            this.$Modal.remove()
            this.name = ''
            this.description = ''
          })
        },
        onCancel: () => {
          this.name = ''
          this.description = ''
        },
        render: (h) => {
          return h('div', {}, [
            h('Input', {
              props: {
                value: this.name,
                autofocus: true,
                placeholder: '请输入部门名称'
              },
              on: {
                input: (val) => {
                  this.name = val
                }
              }
            }),
            h('Input', {
              props: {
                value: this.description,
                autofocus: true,
                placeholder: '请输入部门简介',
                type: 'textarea'
              },
              style: {
                marginTop: '10px'
              },
              on: {
                input: (val) => {
                  this.description = val
                }
              }
            })
          ])
        }
      })
    },
    loadMans (page) {
      this.loadManIng = true
      let data = { id: this.selectItem.id }
      if (page) {
        this.selectItem.jump_page = page
      }
      data.jump_page = this.selectItem.jump_page
      api.member(data, res => {
        this.mans = res.data.data
        this.loadManIng = false
      }, () => {
        this.loadManIng = false
      })
    },
    ref () {
      this.columns = []
      this.listData = []
      this.selectItem = {}
      this.formData = {}
      this.mans = {}
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.title + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData(1)
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>

<style lang="less">
  .tree-item {
    padding: 2px 5px;
    border-radius: 5px;
    cursor: pointer;
  }

  .tree-item:hover {
    background-color: #37a8ff;
    color: white;

  }

  .tree-select-item {
    background-color: #37a8ff;
    padding: 2px 5px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
  }
</style>
